<template lang="">
  <div class="py-24 pt-48 text-center basis-full">
    <h3 class="text-3xl font-bold mb-5 relative after:absolute after:w-8 after:h-0.5 after:bg-slate-800 after:-bottom-2 after:left-2/4 after:-translate-x-2/4">{{ title[0] }}</h3>
    <p class="text-gray-400">{{ desc[0] }}</p>
    <div v-for="e in dataList" :key="e">
      <p>{{ e.name }}</p>
      <p>{{ e.price }}</p>
    </div>
  </div>
  {{ $store.state.number }} <!-- vuex store   data state  number   . -->
  <br />
  {{ number }}
  <br />
  {{ $store.state.number2 }}
  <br />
  <button @click="$store.commit('NumberPlus')"> </button> <!-- store state    props emit  vuex commit . -->
  <br />
  <button @click="$store.commit('NumberMinus')"> </button>
</template>
<script>
import { db } from "../../firebase";
export default {
  name: 'ProductView_1',
  props:{
    title: Array,
    desc: Array
  },
  data() {
    return {
      dataList: [],
      number: 0,
    }
  },
  mounted(){
    // db.("").get().then(() => {   })
    db.collection("product").get().then((data) => {
      data.forEach((e) => {
        console.log(e.data())
        this.dataList.push(e.data());
        return this.dataList
      })
    })
  }, //         .
}
</script>
