import { createStore } from 'vuex'

export default createStore({
  state(){ // vue data
    return {
      number: 100,
      number2: 200,
      loginChk: false,
      displayName: "",
      loginToken: null,
      uid: "",
      noticeId: 0,
      onlineId: 0,
      qnaId: 0,
      galleryId: 0,
    }
  },

  mutations: { // vue    
    NoticeRead(state, payload){
      state.noticeId = payload
    },
    OnlineRead(state, payload){
      state.onlineId = payload
    },
    QnaRead(state, payload){
      state.qnaId = payload
    },
    GalleryRead(state, payload){
      state.galleryId = payload
    },
    NumberPlus(state){
      state.number++ //   vuex this.   state.       props   emit     commit  .
    },
    NumberMinus(state){
      state.number2--
    },
    loginToken(state, payload){
      state.loginToken = payload.refreshToken
      state.loginChk = true
      state.uid = payload.uid
      console.log(payload.uid)
    },
    logOutToken(state){
      state.loginChk = false
      state.loginToken = null
      state.displayName = ''
      state.uid = ''
      sessionStorage.removeItem("refreshToken")
      sessionStorage.removeItem("displayName")
    },
    loginState(state, payload){ // App.vue loginState  .      session date      .
      state.loginToken = sessionStorage.getItem("refreshToken"); // refreshToken  sessiondata 
      state.displayName = payload.displayName
      state.uid = payload.uid // App.vue user.displayName  payload    state.displayName  
      state.loginChk = true // loginChk true 
    }
  },
  actions: {
  },

})
